.cover-section {
	position: fixed;
	z-index: 1000;
	transition: all 1s ease-out;
	background-color: var(--color-primary);
	overflow: hidden;
	width: 100%;
	&.cover-opened {
		transform: translateY(-100%);
	}
	.orn-1 {
		position: absolute;
		top: 0;
		left: 0;
		width: 160px;
		transform: translate(-30%, -20%);
		@include min-md {
			width: 180px;
		}
		@include min-lg {
			width: 200px;
		}
		img {
			z-index: 3;
			transform-origin: 45% 0%;
		}
		.orn-2 {
			width: 180px;
			transform: translate(-30%, -20%);
			@include min-md {
				width: 200px;
			}
			@include min-lg {
				width: 220px;
			}
			img {
				z-index: unset;
				transform-origin: 45% 100%;
			}
		}
	}
	.orn-3 {
		position: absolute;
		top: 0;
		right: 0;
		width: 160px;
		transform: translate(35%, -20%) scaleX(-1);
		pointer-events: none;
		@include min-md {
			width: 180px;
		}
		@include min-lg {
			width: 200px;
		}
		img {
			z-index: 3;
			transform-origin: 45% 0%;
		}
		.orn-4 {
			width: 180px;
			transform: translate(-25%, -20%);
			@include min-md {
				width: 200px;
			}
			@include min-lg {
				width: 220px;
			}
			img {
				z-index: unset;
				transform-origin: 45% 100%;
			}
		}
	}
	.orn-5 {
		position: absolute;
		bottom: 0;
		left: 0;
		width: 200px;
		transform: translate(-10%, 50%);
		@include min-md {
			width: 220px;
		}
		@include min-lg {
			width: 240px;
		}
		img {
			transform-origin: 90% 100%;
		}
		.orn-6 {
			position: absolute;
			top: 0;
			left: 50%;
			width: 80px;
			transform: translate(-50%, -20%);
			@include min-md {
				width: 100px;
			}
			@include min-lg {
				width: 120px;
			}
			img {
				transform-origin: 35% 100%;
			}
		}
	}
	.orn-7 {
		position: absolute;
		bottom: 0;
		right: 0;
		width: 200px;
		transform: translate(20%, 50%);
		@include min-md {
			width: 220px;
		}
		@include min-lg {
			width: 240px;
		}
		img {
			transform-origin: 90% 100%;
		}
		.orn-8 {
			position: absolute;
			top: 0;
			left: 40%;
			width: 80px;
			transform: translate(-50%, -10%) scaleX(-1);
			@include min-md {
				width: 100px;
			}
			@include min-lg {
				width: 120px;
			}
			img {
				transform-origin: 35% 100%;
			}
		}
	}
	.orn-9 {
		position: absolute;
		top: 50%;
		left: 0;
		width: 180px;
		transform: translate(-80%, -50%);
		z-index: 3;
		@include min-lg {
			transform: translate(-90%, -50%);
		}
		img {
			transform-origin: 35% 100%;
		}
	}
	.orn-10 {
		position: absolute;
		top: 50%;
		right: 0;
		width: 180px;
		transform: translate(80%, -50%) scaleX(-1);
		z-index: 3;
		@include min-lg {
			transform: translate(90%, -50%) scaleX(-1);
		}
		img {
			transform-origin: 35% 100%;
		}
	}
	.cover-wrapper {
		position: relative;
		display: flex;
		justify-content: center;
		align-items: flex-start;
		padding: 4rem 0 3rem;
		height: 100vh;
		transition: opacity 1s ease;
		@include max-lg {
			padding: 4rem 0 6rem;
			align-items: center;
		}
		.cover-content {
			text-align: center;
			position: relative;
			width: 100%;
			height: 100%;
			display: flex;
			align-items: center;
			justify-content: center;
			.cover-body {
				position: relative;
				display: flex;
				align-items: center;
				justify-content: center;
				padding: 5rem 2rem;
				border-radius: 500px;
				min-height: 80%;
				@include max-lg {
					min-width: 70%;
				}
				@include max-sm {
					width: 100%;
				}
				.greeting-wrapper {
					position: relative;
					@include max-md {
						font-size: 14px;
					}
					h6 {
						font-weight: 700;
					}
				}
				h6 {
					font-weight: 600;
				}
				.image-wrapper {
					position: absolute;
					top: 0;
					left: 0;
					width: 100%;
					height: 100%;
					&::before {
						position: absolute;
						top: 50%;
						left: 50%;
						width: calc(100% + 1.5rem);
						height: calc(100% + 1.5rem);
						transform: translate(-50%, -50%);
						border: 1px solid var(--color-main);
						border-radius: 500px;
						content: "";
					}
					&::after {
						position: absolute;
						top: 0;
						right: 0;
						bottom: 0;
						left: 0;
						background-color: rgba($color: #000000, $alpha: 0.3);
						border-radius: 500px;
						content: "";
					}
					.cover-background {
						width: 100%;
						height: 100%;
						object-fit: cover;
						border-radius: 500px;
					}
				}
				.cover-description {
					position: relative;
					text-align: center;
					color: var(--color-light);
					z-index: 5;
					* {
						position: relative;
					}
				}
			}
			.elements-widget {
				width: 100%;
			}
		}
	}
}
