.reminder-wrap {
	position: relative;
	display: block;
	padding: 0 0 5rem;
	@include max-md {
		padding: 0 0 3rem;
	}
	.elements-wrapper-absolute {
		.background-element {
			border-radius: 500px;
			overflow: hidden;
		}
	}
	.orn-1 {
		position: absolute;
		top: 0;
		left: 0;
		width: 120px;
		transform: translate(-30%, 0);
		@include min-md {
			width: 140px;
		}
		@include min-lg {
			width: 160px;
		}
		img {
			transform-origin: 35% 100%;
		}
	}
	.orn-2 {
		position: absolute;
		top: 0;
		right: 0;
		width: 120px;
		transform: translate(30%, 0) scaleX(-1);
		@include min-md {
			width: 140px;
		}
		@include min-lg {
			width: 160px;
		}
		img {
			transform-origin: 35% 100%;
		}
	}
	.orn-3 {
		position: absolute;
		bottom: 0;
		left: 0;
		width: 200px;
		transform: translate(-30%, 0) scaleX(-1);
		@include min-md {
			width: 220px;
		}
		@include min-lg {
			width: 240px;
		}
		img {
			transform-origin: 45% 0%;
		}
	}
	.orn-4 {
		position: absolute;
		bottom: 0;
		right: 0;
		width: 200px;
		transform: translate(30%, 0);
		@include min-md {
			width: 220px;
		}
		@include min-lg {
			width: 240px;
		}
		img {
			transform-origin: 45% 0%;
		}
	}
	.orn-5 {
		position: absolute;
		bottom: 2rem;
		left: 0;
		width: 100px;
		transform: translate(-70%, -30%);
		@include min-md {
			width: 120px;
		}
		@include min-lg {
			width: 140px;
		}
		img {
			transform-origin: 35% 100%;
		}
	}
	.orn-6 {
		position: absolute;
		bottom: 2rem;
		right: 0;
		width: 100px;
		transform: translate(70%, -30%) scaleX(-1);
		@include min-md {
			width: 120px;
		}
		@include min-lg {
			width: 140px;
		}
		img {
			transform-origin: 35% 100%;
		}
	}
	.reminder-content {
		position: relative;
		width: fit-content;
		max-width: 100%;
		margin: 0 auto;
		padding: 5rem 3rem;
		color: var(--color-dark);
		&::after {
			position: absolute;
			top: 50%;
			left: 50%;
			width: calc(100% + 1rem);
			height: calc(100% + 1rem);
			border-radius: 500px;
			border: 2px solid var(--color-main);
			transform: translate(-50%, -50%);
			content: "";
		}
	}
	.btn-reminder {
		z-index: 3;
		position: relative;
		margin: 0 auto;
		display: flex;
		align-items: center;
		position: relative;
		gap: 0.5rem;
		transition: all 0.2s ease-in-out;
		text-decoration: none;
		margin-top: 1.5rem;
		path {
			fill: var(--text-button);
			transition: all 0.3s ease-in-out;
		}
	}
}
