.couple-section {
	position: relative;
	text-align: center;
	background-color: var(--color-primary);
	color: var(--color-light);
	.and {
		position: absolute;
		top: 50%;
		left: 50%;
		transform: translate(-50%, -50%);
		font-weight: 500;
		display: flex;
		justify-content: center;
		align-items: center;
		width: 100%;
		margin-bottom: 1rem;
		z-index: 3;
		@include max-md {
			top: 40%;
		}
		span {
			position: relative;
			font-size: 32px;
			font-family: $font-secondary;
		}
	}
	.couple-wrapper {
		position: relative;
		overflow: hidden;
		padding: 5rem 0;
		@include max-md {
			padding: 5rem 0 3rem;
		}
		.orn-99 {
			position: relative;
			width: 80px;
			margin: 0 auto 2rem;
			@include min-md {
				width: 100px;
			}
			@include min-lg {
				width: 120px;
			}
		}
		.orn-1 {
			position: absolute;
			top: 0;
			left: 0;
			width: 160px;
			transform: translate(-30%, -15%);
			@include min-md {
				width: 180px;
			}
			@include min-lg {
				width: 200px;
			}
			img {
				z-index: 3;
				transform-origin: 45% 0%;
			}
			.orn-2 {
				width: 180px;
				transform: translate(-30%, -20%);
				@include min-md {
					width: 200px;
				}
				@include min-lg {
					width: 220px;
				}
				img {
					z-index: unset;
					transform-origin: 45% 100%;
				}
			}
			.orn-3 {
				width: 100px;
				transform: translate(0, -100%);
				@include min-md {
					width: 120px;
				}
				@include min-lg {
					width: 140px;
				}
				img {
					transform-origin: 35% 100%;
				}
			}
		}
		.orn-4 {
			position: absolute;
			top: 0;
			right: 0;
			width: 160px;
			transform: translate(35%, -15%) scaleX(-1);
			pointer-events: none;
			@include min-md {
				width: 180px;
			}
			@include min-lg {
				width: 200px;
			}
			img {
				z-index: 3;
				transform-origin: 45% 0%;
			}
			.orn-5 {
				width: 180px;
				transform: translate(-40%, -20%) scaleX(-1);
				@include min-md {
					width: 200px;
				}
				@include min-lg {
					width: 220px;
				}
				img {
					z-index: unset;
					transform-origin: 45% 100%;
				}
			}
			.orn-6 {
				width: 100px;
				transform: translate(15%, -100%);
				@include min-md {
					width: 120px;
				}
				@include min-lg {
					width: 140px;
				}
				img {
					transform-origin: 35% 100%;
				}
			}
		}
		.orn-7 {
			position: absolute;
			bottom: 1rem;
			left: 0;
			width: 240px;
			transform: translate(-40%, 0) scaleX(-1);
			img {
				transform-origin: 45% 0%;
			}
		}
		.orn-8 {
			position: absolute;
			bottom: 1rem;
			right: 0;
			width: 240px;
			transform: translate(40%, 0);
			img {
				transform-origin: 45% 0%;
			}
		}
		.orn-9 {
			position: absolute;
			top: 50%;
			left: 0;
			width: 120px;
			transform: translate(-70%, -50%);
			img {
				transform-origin: 35% 100%;
			}
		}
		.orn-10 {
			position: absolute;
			top: 50%;
			right: 0;
			width: 120px;
			transform: translate(70%, -50%) scaleX(-1);
			img {
				transform-origin: 35% 100%;
			}
		}
	}
	.couple-body {
		position: relative;
	}
	.couple-content {
		position: relative;
		padding: 3rem 2rem;
		margin-bottom: 18rem;
		&::before {
			position: absolute;
			top: 0;
			right: 0;
			bottom: 0;
			left: 0;
			background: var(--color-main);
			opacity: 0.6;
			border-radius: var(--border-radius);
			content: "";
		}
	}
	.couple {
		position: relative;
		text-align: center;
		padding: 0.01rem;
		&::before {
			position: absolute;
			top: 0;
			right: 0;
			bottom: 0;
			left: 0;
			background: var(--color-primary);
			opacity: 0.25;
			border-radius: var(--border-radius);
			content: "";
		}
		@include max-md {
			&.man {
				margin-bottom: 10rem;
			}
			&.woman {
				margin-top: 10rem;
			}
		}
		.elements-wrapper-absolute {
			overflow: hidden;
			border-radius: var(--border-radius);
		}
		.image-wrap {
			width: 70%;
			position: relative;
			aspect-ratio: 3/4;
			margin: -13rem auto 0;
			border: 2px solid var(--color-main);
			border-radius: 300px;
			.image-element {
				position: relative;
				width: 100%;
				height: 100%;
				padding: 8px;
				.couple-image {
					height: 100%;
					object-fit: cover;
					width: 100%;
					border-radius: 300px;
				}
			}
		}
		.couple-description {
			position: relative;
			padding: 2rem 1rem;
		}
		.couple-parent {
			width: 100%;
		}
		.sosmed-wrap {
			position: absolute;
			bottom: 0;
			left: 50%;
			transform: translate(-50%, 45%);
			width: fit-content;
			max-width: 100%;
			background: var(--color-secondary);
			padding: 0.5rem 0.6rem;
			border-radius: 50px;
			margin: 0 auto;
			.sosmed {
				color: var(--text-button);
				text-decoration: none;
				display: block;
				width: fit-content;
				margin: 0 auto;
				small {
					align-items: center;
					gap: 0.25rem;
					display: flex;
					justify-content: center;
				}
			}
			.fab {
				font-size: 20px;
				@include max-md {
					font-size: 18px;
				}
			}
		}
	}
}
