.story-section {
	position: relative;
	.story {
		position: relative;
		padding: 5rem 0;
		@include max-md {
			padding: 3rem 0;
		}
		.orn-1 {
			position: absolute;
			top: 3%;
			left: 0;
			width: 200px;
			transform: translate(-60%, 0);
			@include min-md {
				width: 280px;
			}
			@include min-lg {
				width: 320px;
			}
			img {
				transform-origin: 45% 100%;
			}
		}
		.orn-2 {
			position: absolute;
			top: 3%;
			right: 0;
			width: 200px;
			transform: translate(70%, 0);
			@include min-md {
				width: 280px;
			}
			@include min-lg {
				width: 320px;
			}
			img {
				transform-origin: 45% 100%;
			}
		}
		.orn-3 {
			position: absolute;
			bottom: 4rem;
			left: 0;
			width: 200px;
			transform: translate(-20%, 0) scaleX(-1);
			@include min-md {
				width: 220px;
			}
			@include min-lg {
				width: 240px;
			}
			img {
				transform-origin: 45% 0%;
			}
		}
		.orn-4 {
			position: absolute;
			bottom: 4rem;
			right: 0;
			width: 200px;
			transform: translate(20%, 0);
			@include min-md {
				width: 220px;
			}
			@include min-lg {
				width: 240px;
			}
			img {
				transform-origin: 45% 0%;
			}
		}
		.orn-5 {
			position: absolute;
			top: 0;
			left: 50%;
			width: 70px;
			transform: translate(-50%, -50%);
			z-index: 5;
			@include min-md {
				width: 90px;
			}
			@include min-lg {
				width: 120px;
			}
			img {
				transform-origin: 35% 100%;
			}
		}
	}
	.story-content {
		position: relative;
	}
	.story-description {
		position: relative;
		text-align: center;
	}
	.image-wrapper {
		position: relative;
		width: 100%;
		height: auto;
		aspect-ratio: 3/4;
		margin-bottom: 3rem;
		.image-element {
			position: relative;
			width: 100%;
			height: 100%;
			border-radius: 500px;
			border: 2px solid var(--color-primary);
			padding: 8px;
			.story-background {
				position: relative;
				object-fit: cover;
				width: 100%;
				height: 100%;
				border-radius: 500px;
			}
		}
	}
}
