.stream-section {
	position: relative;
	padding: 5rem 0;
	text-align: center;
	@include max-md {
		padding: 3rem 0;
	}
	.orn-1 {
		position: absolute;
		top: 0;
		left: 0;
		width: 120px;
		transform: translate(-40%, 0);
		@include min-md {
			width: 140px;
		}
		@include min-lg {
			width: 160px;
		}
		img {
			transform-origin: 35% 100%;
		}
		.orn-2 {
			position: absolute;
			top: 100%;
			left: 0;
			width: 120px;
			transform: translate(-10%, -30%);
			@include min-md {
				width: 140px;
			}
			@include min-lg {
				width: 160px;
			}
			img {
				transform-origin: 35% 100%;
			}
		}
	}
	.orn-3 {
		position: absolute;
		top: 0;
		right: 0;
		width: 120px;
		transform: translate(40%, 0) scaleX(-1);
		@include min-md {
			width: 140px;
		}
		@include min-lg {
			width: 160px;
		}
		img {
			transform-origin: 35% 100%;
		}
		.orn-4 {
			position: absolute;
			top: 80%;
			left: 0;
			width: 180px;
			transform: translate(-40%, -40%);
			@include min-md {
				width: 200px;
			}
			@include min-lg {
				width: 220px;
			}
			img {
				transform-origin: 45% 0%;
			}
			.orn-5 {
				position: absolute;
				top: 100%;
				left: 0;
				width: 320px;
				transform: translate(-45%, 0) scaleX(-1);
			}
			img {
				transform-origin: 45% 100%;
			}
		}
	}
	.orn-6 {
		position: absolute;
		top: 50%;
		left: 0;
		width: 180px;
		transform: translate(-60%, 0);
		@include min-md {
			width: 200px;
		}
		@include min-lg {
			width: 220px;
		}
		img {
			transform-origin: 45% 0%;
		}
	}
	.orn-7 {
		position: absolute;
		bottom: 0;
		right: 0;
		width: 120px;
		transform: translate(50%, 0) scaleX(-1);
		@include min-md {
			width: 140px;
		}
		@include min-lg {
			width: 160px;
		}
		img {
			transform-origin: 35% 100%;
		}
	}
	.stream-wrapper {
		position: relative;
		z-index: 5;
		@include max-lg {
			.row {
				align-items: center;
				gap: 2rem;
			}
		}
		.stream-content {
			position: relative;
			.stream-description {
				position: relative;
				padding: 3rem 2rem;
				&::before {
					position: absolute;
					top: 0;
					right: 0;
					bottom: 0;
					left: 0;
					background: var(--color-primary);
					border-radius: var(--border-radius);
					border-bottom-right-radius: 0;
					border-bottom-left-radius: 0;
					opacity: 0.25;
					content: "";
				}
				@include max-md {
					padding: 2rem 1rem;
				}
			}
			h4 {
				@include max-md {
					text-align: center;
				}
			}
		}
	}
	ul {
		ol {
			padding: 0;
		}
	}
	.btn-custom {
		width: 100%;
		z-index: 10;
		position: relative;
	}
	.yt-embed {
		@include min-lg {
			height: 320px;
		}
	}
	.yt-description {
		position: relative;
		width: 100%;
		@include max-md {
			text-align: center;
		}
	}
}
