.countdown {
	display: grid;
	grid-template-columns: 1fr 1fr;
	text-align: center;
	justify-content: space-evenly;
	position: relative;
	margin: 0 auto;
	width: fit-content;
	gap: 1rem;
	z-index: 3;
	.days,
	.hours,
	.minutes,
	.seconds {
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
		position: relative;
		padding: 1rem;
		&::before {
			position: absolute;
			top: 0;
			right: 0;
			bottom: 0;
			left: 0;
			background: var(--color-main);
			opacity: 0.6;
			border-radius: 12px;
			content: "";
		}
		.huruf {
			font-size: 14px;
			font-weight: 600;
			@include max-xs {
				font-size: 12px;
			}
		}
		.angka {
			margin-bottom: 0;
			font-weight: 500;
			font-size: 38px;
			font-family: $font-secondary;
			@include max-md {
				font-size: 28px;
			}
			@include max-xs {
				font-size: 24px;
			}
		}
		.huruf,
		.angka {
			position: relative;
			margin: 0;
			padding: 0;
		}
	}
}
