.thank-section {
	position: relative;
	padding: 5rem 0 8rem;
	text-align: center;
	@include max-md {
		padding: 3rem 0 8rem;
	}
	.orn-99 {
		position: relative;
		width: 80px;
		margin: 0 auto 2rem;
		@include min-md {
			width: 100px;
		}
		@include min-lg {
			width: 120px;
		}
	}
	.orn-1 {
		position: absolute;
		top: 0;
		left: 0;
		width: 160px;
		transform: translate(-60%, 0);
		@include min-md {
			width: 180px;
		}
		@include min-lg {
			width: 200px;
		}
		img {
			z-index: 3;
			transform-origin: 45% 0%;
		}
		.orn-2 {
			width: 180px;
			transform: translate(0%, -20%);
			@include min-md {
				width: 200px;
			}
			@include min-lg {
				width: 220px;
			}
			img {
				z-index: unset;
				transform-origin: 45% 100%;
			}
		}
	}
	.orn-3 {
		position: absolute;
		top: 0;
		right: 0;
		width: 160px;
		transform: translate(65%, 0) scaleX(-1);
		pointer-events: none;
		@include min-md {
			width: 180px;
		}
		@include min-lg {
			width: 200px;
		}
		img {
			z-index: 3;
			transition-property: 45% 0%;
		}
		.orn-4 {
			width: 180px;
			transform: translate(-10%, -20%) scaleX(-1);
			@include min-md {
				width: 200px;
			}
			@include min-lg {
				width: 220px;
			}
			img {
				z-index: unset;
				transform-origin: 45% 100%;
			}
		}
	}
	.orn-5 {
		position: absolute;
		bottom: 0;
		left: 0;
		width: 260px;
		transform: translate(-35%, 60%);
		@include min-md {
			width: 280px;
		}
		@include min-lg {
			width: 320px;
		}
		img {
			transform-origin: 90% 100%;
		}
		.orn-6 {
			position: absolute;
			top: 0;
			left: 50%;
			width: 80px;
			transform: translate(-50%, -20%);
			@include min-md {
				width: 100px;
			}
			@include min-lg {
				width: 120px;
			}
			img {
				transform-origin: 35% 100%;
			}
		}
		.orn-9 {
			position: absolute;
			top: 0;
			right: 0;
			width: 180px;
			transform: translate(-35%, -70%);
			@include min-md {
				width: 200px;
			}
			@include min-lg {
				width: 220px;
			}
			img {
				transform-origin: 35% 100%;
			}
		}
	}
	.orn-7 {
		position: absolute;
		bottom: 0;
		right: 0;
		width: 260px;
		transform: translate(35%, 60%);
		@include min-md {
			width: 280px;
		}
		@include min-lg {
			width: 300px;
		}
		img {
			transform-origin: 90% 100%;
		}
		.orn-8 {
			position: absolute;
			top: 0;
			left: 50%;
			width: 80px;
			transform: translate(-50%, -10%) scaleX(-1);
			@include min-md {
				width: 100px;
			}
			@include min-lg {
				width: 120px;
			}
			img {
				transform-origin: 35% 100%;
			}
		}
		.orn-10 {
			position: absolute;
			top: 0;
			left: 0;
			width: 180px;
			transform: translate(35%, -70%) scaleX(-1);
			@include min-md {
				width: 200px;
			}
			@include min-lg {
				width: 220px;
			}
			img {
				transform-origin: 35% 100%;
			}
		}
	}
	.image-wrapper {
		position: relative;
		width: 90%;
		height: auto;
		aspect-ratio: 3/4;
		margin: 0 auto;
		&::before {
			position: absolute;
			top: 50%;
			left: 50%;
			width: calc(100% + 1rem);
			height: calc(100% + 1rem);
			transform: translate(-50%, -50%);
			border: 2px solid var(--color-primary);
			border-radius: 500px;
			content: "";
		}
		.image-element {
			position: relative;
			width: 100%;
			height: 100%;
			.thank-background {
				width: 100%;
				height: 100%;
				object-fit: cover;
				border-radius: 500px;
			}
		}
	}
	.thank-wrapper {
		position: relative;
		.thank {
			position: relative;
			padding: 3rem 2rem;
			&::before {
				position: absolute;
				top: 0;
				right: 0;
				bottom: 0;
				left: 0;
				background-color: var(--color-primary);
				opacity: 0.25;
				border-radius: var(--border-radius);
				content: "";
			}
			@include max-md {
				padding: 2rem 1rem;
			}
			p {
				margin-bottom: 0;
			}
		}
	}
}
.footer {
	padding: 1rem 0;
	position: relative;
	text-align: center;
	background-color: var(--color-secondary);
	color: var(--text-button);
	small {
		position: relative;
	}
}
