.rsvp-section {
	position: relative;
	padding: 5rem 0;
	@include max-md {
		padding: 3rem 0;
		.container {
			padding: 0;
		}
	}
	.orn-99 {
		position: relative;
		width: 80px;
		margin: 0 auto 2rem;
		@include min-md {
			width: 100px;
		}
		@include min-lg {
			width: 120px;
		}
	}
	.orn-1 {
		position: absolute;
		top: 0;
		left: 0;
		width: 180px;
		transform: translate(-50%, -10%);
		@include min-md {
			width: 200px;
		}
		@include min-lg {
			width: 220px;
		}
		img {
			transform-origin: 45% 0%;
		}
		.orn-2 {
			width: 320px;
			transform: translate(-50%, 0) scaleX(-1);
			img {
				transform-origin: 45% 100%;
			}
		}
	}
	.orn-3 {
		position: absolute;
		top: 0;
		right: 0;
		width: 180px;
		transform: translate(50%, -10%) scaleX(-1);
		@include min-md {
			width: 200px;
		}
		@include min-lg {
			width: 220px;
		}
		img {
			transform-origin: 45% 0%;
		}
		.orn-4 {
			width: 320px;
			transform: translate(-50%, 0) scaleX(-1);
			img {
				transform-origin: 45% 100%;
			}
		}
	}
	.rsvp-wrapper {
		position: relative;
		background-color: var(--color-primary);
		border-radius: var(--border-radius);
		color: var(--color-light);
		overflow: hidden;
		padding: 3rem 2rem;
		@include max-md {
			padding: 2rem;
		}
		.rsvp-form {
			position: relative;
			.title-section {
				&::before {
					all: unset;
				}
			}
			.form-control,
			.form-select {
				background-color: rgba($color: #fff, $alpha: 0.7);
			}
		}
	}
}
