.header-section {
	position: relative;
	width: 100%;
	.scroll-icon {
		position: relative;
		margin-top: 2rem;
		rect {
			stroke: var(--color-dark);
		}
	}
	.orn-1 {
		position: absolute;
		top: 0;
		left: 0;
		width: 160px;
		transform: translate(-30%, -20%);
		@include min-md {
			width: 180px;
		}
		@include min-lg {
			width: 200px;
		}
		img {
			z-index: 3;
			transform-origin: 45% 0%;
		}
		.orn-2 {
			width: 180px;
			transform: translate(-30%, -20%);
			@include min-md {
				width: 200px;
			}
			@include min-lg {
				width: 220px;
			}
			img {
				z-index: unset;
				transform-origin: 45% 100%;
			}
		}
	}
	.orn-3 {
		position: absolute;
		top: 0;
		right: 0;
		width: 160px;
		transform: translate(35%, -20%) scaleX(-1);
		pointer-events: none;
		@include min-md {
			width: 180px;
		}
		@include min-lg {
			width: 200px;
		}
		img {
			z-index: 3;
			transform-origin: 45% 0%;
		}
		.orn-4 {
			width: 180px;
			transform: translate(-30%, -20%) scaleX(-1);
			@include min-md {
				width: 200px;
			}
			@include min-lg {
				width: 220px;
			}
			img {
				z-index: unset;
				transform-origin: 45% 100%;
			}
		}
	}
	.orn-5 {
		position: absolute;
		bottom: 0;
		left: 0;
		width: 260px;
		transform: translate(-35%, 60%);
		@include min-md {
			width: 280px;
		}
		@include min-lg {
			width: 300px;
		}
		img {
			transform-origin: 90% 100%;
		}
		.orn-6 {
			position: absolute;
			top: 0;
			left: 50%;
			width: 80px;
			transform: translate(-50%, -20%);
			@include min-md {
				width: 100px;
			}
			@include min-lg {
				width: 120px;
			}
			img {
				transform-origin: 35% 100%;
			}
		}
		.orn-9 {
			position: absolute;
			top: 0;
			right: 0;
			width: 200px;
			transform: translate(-20%, -70%);
			@include min-md {
				width: 220px;
			}
			@include min-lg {
				width: 240px;
			}
			img {
				transform-origin: 35% 100%;
			}
		}
	}
	.orn-7 {
		position: absolute;
		bottom: 0;
		right: 0;
		width: 260px;
		transform: translate(35%, 60%);
		@include min-md {
			width: 280px;
		}
		@include min-lg {
			width: 300px;
		}
		img {
			transform-origin: 90% 100%;
		}
		.orn-8 {
			position: absolute;
			top: 0;
			left: 50%;
			width: 80px;
			transform: translate(-50%, -10%) scaleX(-1);
			@include min-md {
				width: 100px;
			}
			@include min-lg {
				width: 120px;
			}
			img {
				transform-origin: 35% 100%;
			}
		}
		.orn-10 {
			position: absolute;
			top: 0;
			left: 0;
			width: 200px;
			transform: translate(20%, -70%) scaleX(-1);
			@include min-md {
				width: 220px;
			}
			@include min-lg {
				width: 240px;
			}
			img {
				transform-origin: 35% 100%;
			}
		}
	}
	.header-content {
		position: relative;
		height: 100vh;
		.header {
			position: relative;
			display: flex;
			align-items: center;
			justify-content: center;
			width: 35%;
			height: 100%;
			margin: 0 auto;
			padding: 4.5rem 0 4rem;
			@include max-lg {
				width: 70%;
			}
			@include max-md {
				width: 100%;
			}
			.header-title {
				position: relative;
				display: flex;
				align-items: center;
				justify-content: center;
				padding: 3rem 2rem;
				width: 100%;
				min-height: 80%;
				text-align: center;
				@include max-md {
					min-height: 90%;
				}
				&::before {
					position: absolute;
					top: 0;
					right: 0;
					bottom: 0;
					left: 0;
					background: var(--color-primary);
					opacity: 0.25;
					border-radius: 500px;
					content: "";
				}
				&-content {
					position: relative;
				}
			}
			#scroll-animate {
				animation: scrollAnim 1500ms ease infinite;
			}
		}
	}
}

@keyframes scrollAnim {
	0% {
		opacity: 0;
		transform: translate3d(0, -20%, 0);
	}
	50% {
		opacity: 1;
	}
	100% {
		transform: translate3d(0, 100%, 0);
		opacity: 0;
	}
}

[data-anim="zoom-in-main"].has-animate {
	animation-name: zoomInMain;
	animation-fill-mode: forwards;
	animation-duration: 3500ms !important;
}

[data-anim="peafowl-right"].has-animate {
	animation-name: peafowlRight;
	animation-duration: 3500ms !important;
	animation-fill-mode: forwards;
	animation-timing-function: linear;
}
[data-anim="peafowl-left"].has-animate {
	animation-name: peafowlLeft;
	animation-duration: 2500ms !important;
	animation-fill-mode: forwards;
	animation-timing-function: cubic-bezier(0.33, 0.12, 0.61, 0.9);
}

@keyframes zoomInMain {
	0% {
		transform: scaleZ(1);
	}
	100% {
		transform: scale3d(1.1, 1.1, 1.1);
	}
}

@keyframes zoomInMain {
	0% {
		transform: scaleZ(1);
	}
	100% {
		transform: scale3d(1.1, 1.1, 1.1);
	}
}

@keyframes peafowlRight {
	0% {
		transform: translate3d(50%, 0, 0);
	}
	20% {
		transform: translate3d(60%, 5%, 0);
	}
	40% {
		transform: translate3d(70%, 0, 0);
	}
	60% {
		transform: translate3d(80%, 5%, 0);
	}
	80% {
		transform: translate3d(90%, 0, 0);
	}
	100% {
		transform: translate3d(100%, 5%, 0);
	}
}

@keyframes peafowlLeft {
	0% {
		transform: translate3d(-50%, 0, 0);
	}
	20% {
		transform: translate3d(-60%, 5%, 0);
	}
	40% {
		transform: translate3d(-70%, 0, 0);
	}
	60% {
		transform: translate3d(-80%, 5%, 0);
	}
	80% {
		transform: translate3d(-90%, 0, 0);
	}
	100% {
		transform: translate3d(-100%, 5%, 0);
	}
}
