.apology-section {
	position: relative;
	padding: 5rem 0;
	@include max-md {
		padding: 3rem 0;
		.container {
			padding: 0;
		}
	}
	.orn-99 {
		position: relative;
		width: 80px;
		margin: 0 auto 2rem;
		@include min-md {
			width: 100px;
		}
		@include min-lg {
			width: 120px;
		}
	}
	.orn-1 {
		position: absolute;
		top: 0;
		width: 320px;
		transform: translate(-90%, 0) scaleX(-1);
		img {
			transform-origin: 45% 100%;
		}
	}
	.orn-2 {
		position: absolute;
		top: 0;
		right: 0;
		width: 320px;
		transform: translate(80%, 0);
		img {
			transform-origin: 45% 100%;
		}
	}
	.orn-3 {
		position: absolute;
		bottom: 0;
		left: 0;
		width: 100px;
		transform: translate(-50%, 10%);
		@include min-md {
			width: 120px;
		}
		@include min-lg {
			width: 140px;
		}
		img {
			transform-origin: 35% 100%;
		}
	}
	.orn-4 {
		position: absolute;
		bottom: 0;
		right: 0;
		width: 100px;
		transform: translate(50%, 10%) scaleX(-1);
		@include min-md {
			width: 120px;
		}
		@include min-lg {
			width: 140px;
		}
		img {
			transform-origin: 35% 100%;
		}
	}
	.appology-wrap {
		position: relative;
		padding: 3rem 2rem;
		background: var(--color-primary);
		color: var(--color-light);
		border-radius: var(--border-radius);
		overflow: hidden;
		@include max-md {
			padding: 2rem;
		}
	}
}
